import React from 'react';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Grid from '@material-ui/core/Grid';
import classNames from 'classnames';
import Icon from '@material-ui/core/Icon';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing.unit,
    },
  });

  const tableContainer = {
    height: 320,
  };
  
  const icon = {
    marginLeft: 10,
  };
  
  class Chat extends React.Component {
    constructor(props) {
          super(props);
  
          this.message = React.createRef();
    }
  
  sendmessage = (e) => {
    e.preventDefault();
    alert('S-a facut!!'+this.message.current.value);
  }
  
    render() {
      const { classes } = this.props; 
  
     return (
    <div>
      <div>
        <h2>Chat</h2>
        <hr/>
      </div>
      <Typography variant="h4" gutterBottom component="h2">
        Chat
      </Typography>
      <div style={tableContainer}>
      <Card>
        <CardContent>
        <p>Chat test!!</p>
        <hr/>
        </CardContent>
        <CardActions>
        <Grid container spacing={12} justify="center" fullWidth>
        <form className={classes.form} method="post" onSubmit={this.sendmessage.bind(this)}>
        <Grid container spacing={12} justify="center">
        <Grid item xs={6}>
        <FormControl margin="normal" required fullWidth>
          <InputLabel htmlFor="message">Message</InputLabel>
          <Input id="message" name="message" autoFocus inputRef={this.message}/>
        </FormControl>
        </Grid>
        </Grid>
        <Grid container spacing={12} justify="center">
          <Grid item xs={6}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
            >
              Send <Icon className={classNames(icon, 'fa fa-paper-plane')} style={icon}/>
            </Button>
          </Grid>
        </Grid>
        
        </form>
        </Grid>
        </CardActions>
      </Card>
      </div>
    </div>
    );
   }
  }
  
  export const Chatstyle = withStyles(styles)(Chat);